html, body {
  font-size: 16px;
  color: #dfdfdf;
  background-color: #063647; }

section {
  padding-top: 3rem;
  padding-bottom: 3rem; }

.section-1 {
  background: #063647; }

.section-2 {
  background-color: #052D3D; }

.section-3 {
  background-color: #0E282E; }

.section-4 {
  background-color: #063647;
  padding: 0.2rem; }

.avatar {
  height: 15vh;
  border-radius: 50%; }

.intro-description {
  padding-top: 1rem; }

.social-container img {
  max-width: 100%;
  max-height: 175px;
  margin-bottom: 15px; }

.project-figure {
  margin: 0 auto 1rem auto;
  max-height: 256px;
  max-width: 256px; }

.project-text {
  text-align: justify; }

.box {
  background-color: transparent;
  border: 2px solid rgba(7, 59, 79, 0.5); }

.title {
  color: #fff;
  text-align: left;
  margin-bottom: 2rem; }

.intro {
  text-align: justify;
  width: 80%;
  margin: 0 auto; }

.void-background {
  background-color: transparent; }

@media screen and (max-width: 768px) {
  .nav-menu {
    background-color: #063647; }
  .nav-toggle:hover {
    background-color: transparent; }
  .nav-toggle span {
    background-color: #dfdfdf; }
  .nav-toggle span:hover {
    background-color: #fff; } }

.p-3 {
  padding-left: 3rem;
  padding-top: 3rem; }

/* experiment  */
*,
:after,
:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.scrollDown {
  position: absolute;
  font-size: 1.4rem;
  bottom: 150px;
  left: 40px;
  line-height: 1;
  letter-spacing: 0.3em;
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
  color: #000;
  z-index: 100; }

.scrollDown:after {
  content: "";
  width: 2px;
  height: 64px;
  background: #000;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: -80px; }

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 40px 0;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  z-index: 1000; }

@media screen and (max-width: 420px) {
  header {
    padding: 20px 6.25%; } }

header.js-color a {
  color: #020b16; }

header.js-color .menuIcon:after,
header.js-color .menuIcon:before {
  background: #020b16; }

header a {
  color: #fff;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }

header a:hover {
  opacity: 0.5; }

header .name {
  font-size: 2.4rem;
  letter-spacing: 0.1em;
  position: relative;
  z-index: 1000; }

@media screen and (max-width: 420px) {
  header .name {
    font-size: 1.6rem; } }

header .wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative; }

header .icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: 6.4rem; }

@media screen and (max-width: 420px) {
  header .icons {
    display: none; } }

header .icons a {
  display: block;
  margin-right: 3.2rem;
  font-size: 2.4rem;
  letter-spacing: 0.1em; }

header .icons a:last-child {
  margin-right: 0; }

header .menuIcon {
  position: relative;
  width: 50px;
  height: 22px;
  cursor: pointer; }

@media screen and (max-width: 420px) {
  header .menuIcon {
    width: 25px;
    height: 15px; } }

header .menuIcon:after,
header .menuIcon:before {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  right: 0;
  -webkit-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -o-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86); }

header .menuIcon:after {
  top: 0; }

header .menuIcon:before {
  bottom: 0; }

header .menuIcon.js-menuOpen:after {
  top: 50%;
  -webkit-transform: rotate(45deg) translate3d(0, -50%, 0);
  transform: rotate(45deg) translate3d(0, -50%, 0); }

header .menuIcon.js-menuOpen:before {
  bottom: 50%;
  -webkit-transform: rotate(-45deg) translate3d(0, 50%, 0);
  transform: rotate(-45deg) translate3d(0, 50%, 0); }

header .global-nav {
  background-image: -webkit-gradient(linear, left top, right top, from(#072142), color-stop(42%, #8c2b7a), to(#ff4d5a));
  background-image: -webkit-linear-gradient(left, #072142, #8c2b7a 42%, #ff4d5a);
  background-image: -o-linear-gradient(left, #072142, #8c2b7a 42%, #ff4d5a);
  background-image: linear-gradient(to right, #072142, #8c2b7a 42%, #ff4d5a);
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -o-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
  z-index: -100; }

header .global-nav.js-open {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

header .global-nav.js-open a {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

header .global-nav__list {
  text-align: center; }

header .global-nav__list li {
  margin-top: 7.2rem;
  position: relative;
  padding: 0 153px;
  overflow: hidden; }

header .global-nav__list li:first-child {
  margin-top: 0; }

@media screen and (max-width: 420px) {
  header .global-nav__list li {
    padding: 0;
    margin-top: 3.2rem; } }

header .global-nav__list a {
  display: inline-block;
  color: #fff;
  font-size: 12rem;
  line-height: 1;
  letter-spacing: 0.1em;
  font-family: "futura_m";
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.2);
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
  -webkit-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s;
  -o-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s;
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s; }

@media screen and (max-width: 800px) {
  header .global-nav__list a {
    font-size: 7.6rem; } }

@media screen and (max-width: 420px) {
  header .global-nav__list a {
    font-size: 3.8rem;
    text-shadow: 0 3px 20px rgba(2, 11, 22, 0.2); } }

header .global-nav__list a:after {
  content: "";
  width: 0;
  height: 10px;
  background: #fff;
  position: absolute;
  left: 0;
  top: 40%;
  -webkit-box-shadow: 0 10px 30px rgba(2, 11, 22, 0.2);
  box-shadow: 0 10px 30px rgba(2, 11, 22, 0.2);
  -webkit-transition: 0.3s cubic-bezier(0.77, 0, 0.175, 1);
  -o-transition: 0.3s cubic-bezier(0.77, 0, 0.175, 1);
  transition: 0.3s cubic-bezier(0.77, 0, 0.175, 1); }

@media screen and (max-width: 800px) {
  header .global-nav__list a:after {
    display: none; } }

header .global-nav__list a:hover {
  opacity: 1; }

header .global-nav__list a:hover:after {
  width: 100%; }

header .global-nav__sns {
  display: none; }

@media screen and (max-width: 420px) {
  header .global-nav__sns {
    display: block; } }

header .global-nav__sns a {
  font-size: 1.8rem;
  margin-right: 2.4rem;
  -webkit-transform: translate3d(0, 150%, 0);
  transform: translate3d(0, 150%, 0); }

header .global-nav__sns a:last-child {
  margin-right: 0; }
